.topbar {
  width: 100%;
  height: 50px;
  background-color: rgb(28, 49, 118);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.topbarWrapper {
  height: 100%;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  font-weight: bold;
  font-size: 30px;
  color: darkblue;
  cursor: pointer;
  color: white;
}

.topRight {
  display: flex;
  align-items: center;
  justify-content: center;
}
.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  color: #555;
}

.topIconBadge {
  position: absolute;
  width: 15px;
  height: 15px;
  top: -5px;
  right: 0px;
  background-color: red;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
}

.topbarAvatar {
  width: 32px;
  height: 32px;
  cursor: pointer;
  border-radius: 50%;
}

.sidebar {
  flex: 1 1;
  height: calc(100vh - 50px);
  background-color: rgb(251, 251, 255);
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
}

.sidebarWrapper {
  padding: 20px;
  color: #555;
}
.sidebarMenu {
  margin-bottom: 10px;
}
.sidebarTitle {
  font-size: 13px;
  color: rgb(103, 101, 101);
}
.sidebarList {
  list-style: none;
  padding: 5px;
}
.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color: rgb(177, 161, 208);
}
.sidebarIcon {
  margin-right: 5px;
  font-size: 20px !important;
}

.container {
  display: flex;
  padding: 0;
  margin: 10px 0;
}

.chart {
  margin: 20px;
  padding: 20px;
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.chartTitle{
    margin-bottom: 20px;
}
.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.featuredItem {
  flex: 1 1;
  margin: 0 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}
.featuredTitle {
  font-size: 20px;
}
.featuredMoneyContainer {
  margin: 10px 0;
  display: flex;
  align-items: center;
}

.featuredMoney {
  font-size: 30px;
  font-weight: 600;
}
.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}
.featuredIcon {
  font-size: 14px;
  margin-left: 5px;
  color: green;
}
.featuredIcon.negative {
  color: red;
}
.featuredSub {
  font-size: 15px;
  color: grey;
}

.home {
  flex: 4 1;
}

